* {
    box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    box-sizing: border-box;
    // font-family:;
}


/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

a,
ol,
ul {
    list-style: none;
    list-style-type: none;
    text-decoration: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none; 
}

img {
    max-width: 100%;
    vertical-align: middle;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: var(--lobster);
    line-height: 1.5;
    color: var(--font-txt-clr);
}

h1 {
    font-size: 48px;
}

h2 {
    font-size: 36px;
}

h3 {
    font-size: 30px;
}

h4 {
    font-size: 24px;
}

h5 {
    font-size: 20px;
}

h6 {
    font-size: 18px;
}

p {
    line-height: 1.5;
    font-weight: 400;
    font-size: 16px;
    margin: 0;
    padding: 0;
}

a {
    &:hover {
        text-decoration: none;
    }
}

body {
    font-family: var(--raleway);
    color: var(--body-txt-clr);
    position: relative;
    &::-webkit-scrollbar {
        width: 8px;
    }
    &::-webkit-scrollbar-track {
        background-color: var(--light-gray-txt);
        box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    }
    &::-webkit-scrollbar-thumb {
        background: var(--theme-bg-clr);
        border-radius: 20px;
    }
    &::after{
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: rgba(0, 0, 0, 0.06);
        z-index: 1;
        opacity: 0;
        visibility: hidden;
        transition: backdrop-filter 0.4s;
        backdrop-filter: blur(10px) opacity(0);
    }
    &.overlay{
        overflow: hidden !important;
        &:after{
            opacity: 1;
            visibility: visible;
            backdrop-filter: blur(10px) opacity(1);
        }
    }
}

.hv2-body {
    &::-webkit-scrollbar-thumb {
        background: var(--themev2-bg-clr);
    }
}
.hv3-body {
    &::-webkit-scrollbar-thumb {
        background: var(--themev3-bg-clr);
    }
}
.hv4-body {
    &::-webkit-scrollbar-thumb {
        background: var(--themev4-bg-clr);
    }
}

