
/* Margin Top */

@for $i from 1 through 20 {
    .mt-#{5 * $i} {
        margin-top: 5px * $i;
    }
}

@for $i from 1 through 10 {
    .mt--#{5 * $i} {
        margin-top: -5px * $i;
    }
}

/* Margin Bottom */

@for $i from 1 through 20 {
    .mb-#{5 * $i} {
        margin-bottom: 5px * $i;
    }
}

@for $i from 1 through 10 {
    .mb--#{5 * $i} {
        margin-bottom: -5px * $i;
    }
}

/* Margin Left */

@for $i from 1 through 20 {
    .ml-#{10 * $i} {
        margin-left: 10px * $i;
    }
}
@for $i from 1 through 10 {
    .ml--#{5 * $i} {
        margin-left: -5px * $i;
    }
}

/* Margin Right */

@for $i from 1 through 20 {
    .mr-#{10 * $i} {
        margin-right: 10px * $i;
    }
}
@for $i from 1 through 10 {
    .mr--#{5 * $i} {
        margin-right: -5px * $i;
    }
}

/* Margin LG */

@include mq(lg) {
    @for $i from 1 through 10 {
        .mt-lg-#{10 * $i} {
            margin-top: 10px * $i;
        }
    }
    @for $i from 1 through 10 {
        .mb-lg-#{10 * $i} {
            margin-bottom: 10px * $i;
        }
    }
    @for $i from 1 through 10 {
        .ml-lg-#{10 * $i} {
            margin-left: 10px * $i;
        }
    }
    @for $i from 1 through 10 {
        .mr-lg-#{10 * $i} {
            margin-right: 10px * $i;
        }
    }
    @for $i from 1 through 10 {
        .mt-lg--#{10 * $i} {
            margin-top: -10px * $i;
        }
    }
    @for $i from 1 through 10 {
        .mb-lg--#{10 * $i} {
            margin-bottom: -10px * $i;
        }
    }
    .mt-lg-0 {
        margin-top: 0;
    }
    .mb-lg-0 {
        margin-bottom: 0;
    }
    .ml-lg-0 {
        margin-left: 0;
    }
    .mr-lg-0 {
        margin-right: 0;
    }
}

