button {
    background-color: transparent;
    border: none;
    border-radius: 0px;
}
button,
.btn {
    &:focus {
        outline: 0;
        box-shadow: none;
    }
}

.btn {
    display: inline-flex;
    padding: 15px 20px;
    line-height: 1;
    font-size: 18px;
    font-weight: 600;
    vertical-align: middle;
    @include transition(.3s);
    cursor: pointer;
    text-transform: capitalize;
    border: none;
    position: relative;
    z-index: 1;
    color: var(--white-txt-clr);
    border-radius: 0;
    &:hover {
        color: var(--white-txt-clr);
    }
}


// Btn Theme

.btn-anime,
.btn-skew-y{
    position: relative;
    overflow: hidden;
    z-index: 1;
    &::before,
    &::after {
        position:absolute;
        left: 0;
        width: 100%;
        height: calc( 100% + 1px);
        line-height: 1; 
        font-size: 18px;
        font-weight: 600;
        @include transition(.3s);
        cursor: pointer;
        text-transform: capitalize;
        z-index: 999;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &::before{
        content: attr(data-text);
    }
}
.btn-skew-y{
    border: 1px solid transparent;
    background: var(--theme-bg-clr);
    span{
        transform: scale3d(0.5, 0.5, 1);
        opacity: 0;
        visibility: hidden;
        height: calc(100% + 2px);
        width: 100%;
        display: block;
        @include transition (.3s);
    }
    &::before, 
    &::after{
        @include transition (.3s);
        background-color: var(--theme-bg-clr);
    }
    &::after {
        top: 0;
        content: attr(data-text);
        clip-path: polygon(-2% 0, 101% 0, 101% 50%, -2% 50%);
    }
    &::before {
        bottom: -1px;
        clip-path: polygon(-2% 49%, 101% 49%, 101% 100%, -2% 100%);
    }
    &:hover{
        background-color: var(--white-bg-clr);
        color: var(--theme-txt-clr);
        border-color: var(--theme-bdr-clr);
        span{
            transform: scale3d(1, 1, 1);
            opacity: 1;
            visibility:visible;
        }
        &::after, 
        &::before {
            height: 0;
            opacity: 0;
            visibility:hidden;
        }
        &::before {
            clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0 100%);
        }
        &::after {
            clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
        }
    }
}
.btn-anime {
    background: var(--themev2-bg-clr);
    border-radius: 100px;
    span{
        transform: translateX(0);
        opacity: 1;
        visibility: visible;
        @include transition(.3s);
        color: var(--font-txt-clr);
    }
    &:before{
        height: 100%;
        transform: translateX(-100%);
        opacity: 0;
        visibility: hidden;
        background-color: var(--white-bg-clr);
        top: 0; 
        border-radius: 100px;
        color: var(--themev2-txt-clr);
        border: 1px solid var(--themev2-bdr-clr);
    }
    &:hover{
        span{
            transform: translateX(100%);
            opacity: 0;
            visibility: hidden;
        }
        &:before{
            opacity: 1;
            visibility:visible;
            transform: translateX(0);
        }
    }
}

.theme3-btn {
    background: var(--themev3-bg-clr);
    border-radius: 5px;
    span {
        color: var(--white-txt-clr);
    }
    &:before{
        border-radius: 5px;
        color: var(--themev3-txt-clr);
        border: 1px solid var(--themev3-bdr-clr);
    }
}
.theme4-btn {
    background: var(--themev4-bg-clr);
    border-radius: 5px;
    span {
        color: var(--white-txt-clr);
    }
    &:before{
        border-radius: 5px;
        color: var(--themev4-txt-clr);
        border: 1px solid var(--themev4-bdr-clr);
    }
}



// Light Gray Button
.l-gray-btn {
    &::before,
    &::after {
        color: var(--font-txt-clr);
        background-color: var(--light-gray-bg);
    }
    &:hover {
        border-color: var(--font-bdr-clr);
        color: var(--font-txt-clr);
    }
}





.btn-lg {
    padding: 15px 30px;
}
.btn-md {
    padding: 15px 20px;
}

.btn-sm {
    padding: 12px 20px;
}

// Boder Radious
.bdr-5 {
    border-radius: 5px;
}



// Responsive 

@media screen and (max-width: 767px){
   .btn {
    //    padding: 12px 20px;
       font-size: 16px;
   }
    .btn-anime {
        &::before {
           font-size: 16px;
        }
    }
   .btn-lg {
    padding: 13px 26px;
    }
}