
.input-box {
    label {
        display: block;
        font-size: 18px;
        font-weight: 500;
        color: var(--font-txt-clr);
        line-height: 1;
        margin-bottom: 20px;
    }
}

.input-fild {
    position: relative;
    &:focus-within {
        input,textarea {
            border-color: var(--theme-bdr-clr);
        }
    }
    input,
    textarea {
        width: 100%;
        outline: none;
        border: 1px solid var(--light-gray-bdr);
        height: 60px;
        background: var(--ls-gray-bg);
        color: var(--font-txt-clr);
        padding: 20px 70px 20px 20px;
        @include transition(.3s);
        &::-moz-placeholder {
            color: var(--body-txt-clr);
        }
        &::placeholder {
            color: var(--body-txt-clr);
        }
    }
    button {
        position: absolute;
        content: "";
        background: var(--theme-bg-clr);
        width: 60px;
        height: 100%;
        right: 0;
        top: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        img,
        svg {
            max-width: 18px;
            max-height: 18px;
            color: var(--white-txt-clr);
            font-size: 24px;
        }
    }
}



// Nice Select
.nice-select {
    background: transparent;
    border:1px solid var(--light-gray-bdr);
    padding: 20px;
    color: var(--white-txt-clr);
    border-radius: 0px;
    cursor: pointer;
    font-size: 18px;
    font-weight: 400;
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    transition: 0s;
    position: relative;
    .option {
        font-size: 16px;
    }
    &:focus {
        border-color:var(--theme-bdr-clr);
    }
    &::after {
        border-bottom: none;
        border-right: none;
        content: url('https://api.iconify.design/akar-icons/chevron-down.svg');
        margin-top: unset;
        pointer-events: none;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
        width: 10px;
        height: 10px;
        transition: all .2s ease-in-out;
    }
    &.open {
        &::after {
            transform: rotateX(-180deg);
            top: 40%;
        }
    }
    .list {
       background-color: #fdfdfd;
       width: 100%;
       position: absolute;
       color: #000;
       border-radius: 0;
       z-index: 999;
    }
}



// Responsive 
@media screen and (max-width: 767px) {
    .input-box {
        label {
            font-size: 16px;
            margin-bottom: 10px;
        }
    }
    .input-fild {
        input {
            height: 50px;
        }
        button {
            width: 50px;
            img,
            svg {
                max-width: 16px;
                max-height: 16px;
            }
        }
    }
    .nice-select {
        font-size: 16px;
        height: 50px;
        .option {
            font-size: 16px;
        }
    }
}